$color-white: #e7d5d5;
#nav>li{
  color: $color-white;
  font-size: 30px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 900;
  border-left: 3px solid $color-white;
  border-radius: 3px;
  padding-left: 5px;
}


#nav{
  background-color: #2f3035;
  position: fixed;
  top: 0;
  display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  
  width: 100%;
  max-height: 130px;
  // border: 4px solid blue;
  border-radius: 10px;
  
  list-style: none;
}

a, a:visited{
  color: #c3cad4;
  transition: 0.5s;
  border-radius: 5px;
}
a:hover{
  background-color: #2d9ac5da;
}

@media screen and (max-width: 647px) {
  #nav {
    justify-content: space-between;
  }
  h1{
    margin-top: 90px;
  }
  p{
    font-size: 10px;
  }
  @media screen and (max-width: 536px){
    #nav{
    flex-direction: column;
    }
     h1{
      margin-top: 140px;
    }
  }
}