
$color-important: #2c90af;
$color-less-important: #36c2af;


*{
  word-wrap: break-word;
  box-sizing: border-box;
  margin: 0;
}
body{
  width: 100%;
}
h1{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 50px;
  color: $color-important;
  text-align: center;
  margin-bottom: 25px;
  margin-top: 70px;
}
h2{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 50px;
  color: $color-less-important;
  text-align: center;
  margin-bottom: 25px;
  border-radius: 50px;
  
}
strong {
  height: 100%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: #adacb8;
  border-radius: 10px;
  font-size: 0.8em;
}
em{
  color: #10c4d4;
}
ul{
  text-align: center;
}
.fa-arrow-down{
  // margin: auto;
  text-align: center;
  width: 100%;
  font-size: 50px;
}

.fa-hand-point-right{
  width: 80%;
  font-size: 50px;
  text-align: left;
  font-size: 40px;
}