$width-scrollbar: 10px;

p{
    font-size: 30px;
    font-family:  Georgia, 'Times New Roman', Times, serif;
    text-align: justify;

    color: #d4c4b2;
    font-weight: 1;
    border-radius: 10px;
    transition: 1s;

}
@media screen and (max-width: 647px) {
    p{
      font-size: 23px;
    }
}
article{
    width: 90%;
    margin: auto;
}
section{
    background: linear-gradient(20deg, #22558f 0%, #34116d 45%, #6628d1 70%, #22558f 100%);  


    width: 100%;
    max-height: calc(100vh - 20px);
    
    border-radius: 10px;
    padding: 20px 0 20px $width-scrollbar;

    overflow: auto ;

    h1{
        margin-top: 10px;
    }
}
section::-webkit-scrollbar {
    width: $width-scrollbar;
}
section::-webkit-scrollbar-track{
    background: #00000000;
}
section::-webkit-scrollbar-thumb{
    background-color: #0c828d;
    border-radius: 10px;
}
section::-webkit-scrollbar-thumb:hover{
    background-color: #730077;
}